/* App.css */

.navbar {
  background-color: #333;
  color: #fff;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.page-name {
  font-size: 24px;
  font-weight: bold;
}

.button-container {
  display: flex;
}

.button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  margin: 0 10px;
  text-decoration: none;
}

.button:hover {
  background-color: #0056b3;
}
